<template>
    <div class="addtodo">
        <h3>添加用户</h3>
        <form @submit.prevent="onsub">
            <input type="text" placeholder="...请添加待办事项" v-model="title">
            <input type="submit" value="添加">
        </form>
    </div>
</template>

<script>
    import {mapActions} from 'vuex'
    export default {
        name: "addtodo",
        data(){
            return {
                title:'',
            }
        },
        methods:{
            ...mapActions(['addTodo']),
            onsub(){
               this.addTodo(this.title);
               this.title='';
            }
        }
    }
</script>

<style scoped>
    form{
        display: flex;
    }

    input[type='text']{
        flex-grow: 10;
        padding: 5px;
    }
    input[type='submit']{
        flex-grow: 2;
        background: #41b883;
        border: none;
        padding: 5px;

    }


</style>