const state={
    todos:[]
};

const getters={
    alltodos:state=>state.todos
};

const mutations={
    setTodos:(state,todos) =>{
        state.todos = todos;
    },
    newtodo:(state,todo)=>{
        state.todos.unshift(todo);
    },
    removetodo:(state,id)=>{
        state.todos=state.todos.filter(todo=>todo.id!==id);
    },
    updatet:(state,utodo)=>{
       const index=state.todos.findIndex(todo=>todo.id===utodo.id);
       if(index!==-1){
           state.todos.splice(index,1,utodo);
       }
    }
};


const actions={
    async fetchTodos({commit}){
      const res=await axios.get('http://jsonplaceholder.typicode.com/todos');
        // console.log(res);
        commit('setTodos',res.data);
    },
    async addTodo({commit},title){
        const res = await axios.post('http://jsonplaceholder.typicode.com/todos',{title,completed:false});
        // console.log(res);
        commit('newtodo',res.data);
    },
    async deleteTodo({commit},id){
        await axios.delete(`http://jsonplaceholder.typicode.com/todos/${id}`);
        commit('removetodo',id);
    },
    async filterTodos({commit},count){
        const res=await axios.get(`http://jsonplaceholder.typicode.com/todos?_limit=${count}`);
        // console.log(res);
        commit('setTodos',res.data);
    },
    async updateTodo({commit},utodo){
        const res=await axios.put(`http://jsonplaceholder.typicode.com/todos/${utodo.id}`,utodo);
        // console.log(res);
        commit('updatet',res.data);
    }
};


export default ({
    state,
    getters,
    mutations,
    actions
})