<template>
    <div>
        <h3>待办事项</h3>
        <div class="legend">
            <span>双击表示完成</span>
            <span><span class="incomplete-box"></span>= 未完成</span>
            <span><span class="complete-box"></span>= 已完成</span>
        </div>
        <div class="todos">
            <div class="todo"  :class="{'is-completed':todo.completed}" @dblclick="onDblclick(todo)" v-for="(todo,index) in alltodos" :key="index">
                {{todo.title}}
                <i class="fas fa-trash-alt" @click="deleteTodo(todo.id)"></i>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapGetters,mapActions } from 'vuex'
    export default {
        name: "todos",
        computed:mapGetters([
            'alltodos',
        ]),
        methods:{
            ...mapActions(["fetchTodos",'deleteTodo','updateTodo']),
            onDblclick(todo){
                let utodo = {
                    title:todo.title,
                    id:todo.id,
                    completed:!todo.completed
                };
                this.updateTodo(utodo);
            }

        },
        created() {
            this.fetchTodos();
        }
    }
</script>

<style scoped>
.todos{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1rem;
}
.todo{
    border: 1px solid #cccccc;
    background: #41b883;
    padding: 1rem;
    border-radius:5px;
    text-align: center;
    position: relative;
    cursor: pointer;
    }
.legend {
    display: flex;
    justify-content: space-around;
    margin-bottom: 1rem;
}

.complete-box {
    display: inline-block;
    width: 10px;
    height: 10px;
    background: #35495e;
}

.incomplete-box{
    display: inline-block;
    width: 10px;
    height: 10px;
    background: #41b883;
}

.is-completed{
    background: #35495e;
    color: #ffffff;
}

@media (max-width:500px) {
    .todos{
        grid-template-columns: 1fr;
    }
}


i{
    position: absolute;
    right: 10px;
    bottom: 10px;
    cursor: pointer;
    color: #fff;
}
</style>